@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root{
  --primary-white:#fff;
  --primary-orange:#f9ab00;
  --primary-dark:rgba(255,255,255,0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;

}
body{
  font-family: "Roboto Condesed" sans-serif;
}
/************************ Navbar ***********************************/
nav{
  z-index: 2;
}
.logo{
  width:12.25rem;
  height:0.5;
}

/************************Menu links***********************************/
nav a.nav-link{
  font-weight: 500;
  letter-spacing:0.0375rem;
  cursor:pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem; 
  color: #fff !important; 
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255,255,255,0);
  border-top: 0.1875rem solid var(--primary-dark);
}
.navbar-brand{
  max-width:17.25rem;
  padding:0 !important;
}

@media(max-width:768px)
{
  .navbar-brand{
    width: 7rem  !important;

  }}

.navbar-nav > li > a.active,  .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/************************ Header ***********************************/
.header-wraper{
  position:relative;
  background: url(/rizwanah.github.io/static/media/headerimage.7b66e183.jpg) no-repeat; 
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display:flex;
  position:absolute;
  top:0;
  left:0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Robot Condesed" , sans-serif;
  z-index: 1;

}
.main-info h1{
  color:#f9ab00;
  color:var(--primary-orange);
  text-transform: uppercase;
}

.fakeimg{
  position:relative;
  background: url(/rizwanah.github.io/static/media/r_mustafa3.4b4cff29.jpg) no-repeat; 
  background-size: cover;
  background-position: center;
  height: 90vh;
}
/************************ Typed Text ***********************************/
.typed-text{
  font-size: 2rem;
  color:#fff;
  color:var(--primary-white);
}

@media(max-width:768px)
{
  .main-info h1{
    font-size: 1.5rem !important;

  }
  .typed-text{
    font-size: 1.3rem !important;
  }
}

/************************ Header main offer button ***********************************/
.btn-main-offer{
  border: 1px solid #fff;
  border: 1px solid var(--primary-white);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color:#fff;
  color:var(--primary-white);
  transition: .2s ease-in-out;
}

/************************ Particles JS ***********************************/

.tsparticles-canvas-el{
  position: absolute;
  height:100vh;
  width:100vw;
  z-index: 1;
}

@media(max-width:768px){
  .particles-canvas{
    display:none;
  }
}
/************************ About us Divider ***********************************/
.divider{  
margin: 1px auto;
align-items: center;
max-width: 600px;
position:relative;
}


.divider:after{
  content: "";
  display:block;
  width: 120px;
  height:1px;
  background:#f9ab00;
  position: absolute;
  right: 0;
  top: 50%;
  z-index: -2;
  }

  .divider:before{
    content: "";
    display:block;
    width: 120px;
    height:1px;
    background:#f9ab00;
    position: absolute;
    left: 0;
    top: 50%;
    z-index: -2;
    }

    .align{
      color: #fff;
      color: var(--primary-white); 
      padding-left: 0.8rem ;
      }

      .icon{
        padding-left: 0.8rem ;
        color:#f9ab00;
      }

      .bgcolor{
        background-color: rgb(29, 27, 27);
      }
